import React from "react";
import classes from "./Faq.module.css";
import FaqItem from "./FaqItem";

const Faq = (props) => {
  return (
    <div
      style={{
        minHeight: "70vh",
        background:
          "linear-gradient(180deg, #0C4471 0%, #00233F 100%), #002440",
        zIndex: 10,
        position: "relative",
      }}
      id="faq"
    >
      <div className={classes.Faq}>
        <FaqItem
          question={"Koja je tema FON Hakatona?"}
          answer={
            "Za sada je tema Hakatona tajna, ali e biti otkrivena na brifingu odmah nakon otvaranja takmienja."
          }
        />
        {/* <FaqItem
          question={'Da li je mogua individualna prijava?'}
          answer={
            'Naalost, nije mogue prijaviti se individualno, jer su za uee neophodni timovi.'
          }
        /> */}
        <FaqItem
          question={" Koliko lanova tim moe imati?"}
          answer={
            "Tim treba da sadri od 3 do 4 lana.  Nije mogua individualna prijava, jer su za uee neophodni timovi."
          }
        />
        <FaqItem
          question={"Da li e nam biti dostupne konsultacije sa mentorima?"}
          answer={
            "Da. Tokom izrade reenja, u odreenim terminima, takmiari e imati mogunost konsultacije sa mentorima. Mentorsku podrku tokom takmienja e initi  nastavnici i saradnici sa fakulteta kao i alumni lanovi  FONIS-a."
          }
        />
        {/* <FaqItem
          question={'Ko moe takmiarima sve biti mentor?'}
          answer={
            'Mentorsku podrku tokom takmienja e initi  nastavnici i saradnici sa fakulteta kao i alumni lanovi  FONIS-a.'
          }
        /> */}
        <FaqItem
          question={
            "Da li e se takmienje ove godine odrati uivo ili online?"
          }
          answer={
            "Ove godine e se takmienje odrati uivo u celosti. Takmiarima e na raspolaganju biti prostorije Fakulteta organizacionih nauka, gde je cilj da se za 24h osmisli najprikladnije softversko reenje na zadatu temu. Takmienju e prethoditi selekcija koja e biti odrana online."
          }
        />
        <FaqItem
          question={
            "Koje tehnologije dolaze u obzir za razvoj softverskog reenja?"
          }
          answer={"Takmiari mogu koristiti tehnologije po svom izboru."}
        />
        <FaqItem
          question={"Ko se sve moe prijaviti za uee na Hakatonu?"}
          answer={
            "Pravo uea na Hakatonu imaju svi stariji od 16 i mlai od 26 godina kako iz Srbije tako i iz inostranstva, bez obzira na dravljanstvo ili nivo formalnog obrazovanja."
          }
        />
        <FaqItem
          question={
            "Kako izgleda bodovanje pri selekciji za uee na takmienju?"
          }
          answer={
            "Od ove godine selekciju ine tri dela, najpre sama prijava, zatim test znanja i konano algoritamski zadaci. Pri pregledanju Vaih prijava, cilj nam je da uoimo motivaciju. Potrebno je da svi lanovi tima priloe i CV koji nam pomae da bolje sagledamo vae znanje, iskustvo i vetine kada je programiranje u pitanju. Test znanja se radi online i timski na Moodle platformi. Poslednji deo selekcije je test algoritamskih zadataka koji se, takoe, radi online i timski."
          }
        />
      </div>
      <div className={classes.chevron}></div>
      <div
        style={{
          width: "100%",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          marginTop: "-35px",
        }}
      ></div>
    </div>
  );
};

export default Faq;
